        step: 1,
        client_logo: null,
        client_logo_name: null,
        comment: 'What a fantastic shot! We loved your post. Would you mind us utilizing it for our brand?',
        tncName: "termAndConditions.docx",
        tncUrl: "termAndConditions.docx",
